import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			newMobile: '',
			newCode: '',
			code: '获取验证码',
			disabled: false,
			msgCode: '',
			num: 60,
			oldMobile:'',
			id:''
		}
	},
	onLoad(option) {
		// this.oldMobile = option.oldMobile;
		this.id=option.id;
	},
	methods: {
		// 获取验证码
		getCode() {
			if (this.newMobile != '') {
				var reg = /^(1[0-9])\d{9}$/;
				if (!reg.test(this.newMobile)) {
					this.$api.msg('请输入正确的手机号码');
				} else {
					let obj = {
						telephone: this.newMobile
					}
					axios.post('/sso/user/sendCode', obj).then(res => {
						if (res.data.code = '200') {
							this.$api.msg('验证码发送成功');
							this.disabled = true;
							this.code = this.num + 's';
							var timer = setInterval(() => {
								this.num--;
								this.code = this.num + 's';
								if (this.num == 0) {
									clearInterval(timer);
									this.code = '重新发送';
									this.num = 60;
									this.disabled = false;
								}
							}, 1000)
						}
					})
				}
			} else {
				this.$api.msg('请输入手机号')
			}
		},
		// 修改手机号
		save() {
			var reg = /^(1[0-9])\d{9}$/;
			if(this.newMobile==''){
				this.$api.msg('请输入手机号')
			}else{
				if(!reg.test(this.newMobile)){
					this.$api.msg('请输入正确的手机号码');
					return;
				}else if(this.newCode==''){
					this.$api.msg('请输入验证码');
				}else{
					let obj ={
						  authCode: this.newCode,
						  oldPhone: uni.getStorageSync('setPhone')||this.$store.state.userInfo.phone,
						  phone: this.newMobile
					}
					axios.post('/sso/user/bindNewPhone',obj).then(res=>{
						if(res.data.code==200){
							this.$api.msg('修改成功');
							uni.navigateTo({
								url:'set?id='+this.id
							})
						}else{
							this.$api.msg(res.data.data);
						}
					})
				}
			};
		}
	}
}
